@import "reset.scss";
@import "fonts.scss";
@import "grid.scss";
@import "vars.scss";


body {
	background-color: #FFFFFF;
	font-family: BloggerSans, sans-serif;
	font-size: 18px;
	line-height: 1;
	color: $main-dark;
}

h1 {
	font-size: 50px;
	font-weight: 700;
	@media screen and (max-width : 991px) {
	 font-size: 30px;
	}
	@media screen and (max-width : 767px) {
	 font-size: 22px;
	}
}

h3 {
	font-size: 30px;
	font-weight: 700;

	@media screen and (max-width : 767px) {
		font-size: 22px;
	}
}

h4 {
	font-size: 22px;
	font-weight: 700;
}

h5 {
	font-size: 20px;
	font-weight: 700;
}

h6 {
	font-size: 18px;
	font-weight: 700;
}

img {
	max-width: 100%;
}

input {
	outline: 0;
}

a {
	transition: $transition;
}

button {
	outline: 0;
}

a:hover {
	color: $main-accent;
}

.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	padding-right: 19px;
	padding-left: 19px;
	height: 50px;
	font-size: 16px;
	font-weight: 700;
	transition: $transition;

	&.btn-primary {
		border: 1px solid #DFDFDF;

		&:hover {
			border-color: $main-accent;
			color: $main-accent;
		}
	}

	&.btn-secondary {
		background: $main-accent;
		color: $main-light;

		&:hover {
			background: darken($main-accent, 10%);
		}
	}

	&.btn-outline {
		border: 2px solid $main-light;
		background-color: transparent;

		&:hover {
			border-color: $main-accent;
			color: $main-accent;
		}
	}

	@media screen and (max-width : 599px) {
		height: 40px;
	}
}


.form-group {
	position: relative;
	width: 300px;

	input {
		border: 1px solid #EEEEEE;
		border-radius: 10px;
		padding: 15px 19px;
		height: 50px;
		width: 100%;
	}

	textarea {
		border: 1px solid #EEEEEE;
		border-radius: 10px;
		padding: 15px 19px;
		height: 50px;
		width: 100%;
	}

	::placeholder {
		font-size: 16px;
	}
}

.search-form {
	.form-group {
		width: 370px;
	}

	.form-group__button {
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);
	}
}




.top-line__inner {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #EEEEEE;
	padding-top: 19px;
	padding-bottom: 19px;

	.mobile-trigger {
		display: none;
		margin-left: auto;

		@media screen and (max-width : 767px) {
			display: block;
		}
	}
}

.top-line {

	.top-line__menu,
	.top-line__socials {
		@media screen and (max-width : 767px) {
			display: none;
		}
	}
}


.nav-list {
	display: flex;
	justify-content: space-between;
	align-items: center;

	.nav-item {
		margin-right: 35px;
		font-size: 16px;
		color: $secondary-dark;

		&:hover {
			text-decoration: underline;
		}

		@media screen and (max-width : 991px) {
			margin-right: 20px;
			font-size: 14px;
		}
	}
}

.socials {
	display: flex;
	align-items: center;

	.socials-item {
		display: flex;
	}

	.socials-item+.socials-item {
		margin-left: 20px;
	}

	.socials-link {
		font-size: 16px;
	}
}

.header-main__inner {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 33px;
	padding-bottom: 34px;

	@media screen and (min-width : 768px) and (max-width : 991px) {
		justify-content: flex-end;
	}
}

.header-main__logo {
	width: 270px;

	@media screen and (max-width : 991px) {
		margin-right: auto;
	}
}

.header-main__info {
	margin-right: 6px;
	border-right: 1px solid #EEEEEE;
	padding-right: 10px;

	@media screen and (max-width : 767px) {
		border-right-width: 0;
	}

	@media screen and (max-width : 550px) {

		.contacts-email,
		.working-hours__text {
			display: none;
		}
	}
}

.contacts {
	margin-bottom: 7px;
}

.contacts-item {
	display: inline-flex;
	margin-right: 10px;

	i {
		margin-right: 10px;
	}
}

.contacts-phone {
	font-weight: 700;

	@media screen and (max-width : 767px) {
		font-size: 16px;
	}
}

.contacts-email {
	font-size: 16px;
}

.working-hours {
	font-size: 15px;
	color: $secondary-dark;

	@media screen and (max-width : 767px) {
		font-size: 14px;
	}
}

.header-main__actions {
	display: flex;
	justify-content: space-between;
	font-size: 40px;

	.cart {
		margin-left: 20px;
	}
}

.cart {
	position: relative;
	display: inline-block;
}

.cart-contain {
	position: absolute;
	top: -7px;
	right: -11px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 2px solid $main-light;
	border-radius: 12px;
	background-color: $main-accent;
	height: 24px;
	min-width: 24px;
	font-size: 12px;
	color: $main-light;
}

.cart-contain__inner {
	padding: 4px;
}

.header-cats__inner {
	display: flex;
	justify-content: space-around;
	align-items: center;
	border-radius: 10px;
	height: 70px;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);

	@media screen and (max-width : 767px) {
		cursor: pointer;
	}
}

.header-cats__list {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-basis: 100%;
	padding-right: 39px;
	padding-left: 39px;

	@media screen and (max-width : 767px) {
		display: none;
	}
}

.header-cats__item {
	padding: 0 10px;
	font-weight: 700;
	color: #151515;
	text-align: center;

	@media screen and (max-width : 991px) {
		font-size: 16px;
	}
}


.section:not(:last-child) {
	padding-bottom: 75px;

	@media screen and (max-width : 767px) {
		padding-bottom: 40px;
	}
}

.sec-hero {
	position: relative;
	padding-top: 50px;

	&::before {
		position: absolute;
		top: -70px;
		right: 0;
		content: '';
		background-image: url(../img/hero-decor.svg);
		background-size: cover;
		background-repeat: no-repeat;
		height: 143px;
		width: 82px;

		@media screen and (max-width : 1365px) {
			display: none;
		}
	}
}

.hero {
	.btn {
		padding-right: 30px;
		padding-left: 30px;
		background: $main-light;
		font-size: 18px;
		color: $main-dark;

		@media screen and (max-width : 767px) {
			font-size: 16px;
		}
	}

	@media screen and (max-width : 1099px) {

		.col-8,
		.col-4 {
			flex-basis: 100%;
			max-width: 100%;
		}
	}
}

.hero__main {
	display: flex;

	.hero__item {
		position: relative;
		padding-top: 125px;
		padding-bottom: 135px;
		padding-left: 70px;
		background: url(../img/hero-pattern.png), linear-gradient(99.15deg, #50A6E3 0%, #A7DAFF 101.08%);
		background-size: cover;
		background-repeat: no-repeat;

		&::before {
			position: absolute;
			top: -18px;
			right: 8px;
			content: '';
			background: url(../img/hero-girl.png);
			background-repeat: no-repeat;
			height: 500px;
			width: 425px;

			@media screen and (max-width : 767px) {
				right: calc(50% - 210px);
				transform: scale(0.5);
			}
		}

		@media screen and (max-width : 767px) {
			padding: 30px;
			height: 450px;
			width: 100%;
		}
	}

	.hero__item-content {
		padding-right: 330px;

		@media screen and (max-width : 767px) {
			justify-content: space-between;
			align-items: center;
			padding-right: 0;
			width: 100%;
		}
	}

	.hero__item-title {
		margin-bottom: 15px;
		font-size: 50px;
		font-weight: 700;

		.light {
			font-weight: 400;
		}

		@media screen and (max-width : 767px) {
			font-size: 30px;
		}
	}

	.hero__item-text {
		font-size: 18px;
	}

	@media screen and (max-width : 767px) {
		width: 100%;
	}
}

.hero__side {
	display: flex;
	flex-direction: column;

	.hero__item {
		&:not(:last-of-type) {
			margin-bottom: 30px;

			@media screen and (max-width : 1099px) {
				margin-right: 30px;
			}
		}
	}

	.hero__item {
		padding-right: 110px;
	}

	.hero__item-title {
		margin-bottom: 12px;
	}

	@media screen and (max-width : 1099px) {
		flex-direction: row;
		margin-top: 30px;

		.hero__item {
			flex-basis: 50%;
			max-width: 50%;
		}
	}

	@media screen and (max-width : 767px) {
		display: none;
	}
}

.hero__item {
	position: relative;
	border-radius: 10px;
	padding-left: 28px;
	height: 100%;
	color: $main-light;
}

.hero__item--2 {
	background: linear-gradient(0deg, rgba(0, 141, 65, 0.2), rgba(0, 141, 65, 0.2)), linear-gradient(113.62deg, #2AB86B 0%, #5DE39A 97.37%);

	&::before {
		position: absolute;
		top: 10px;
		right: -10px;
		content: '';
		background: url(../img/hero-prod-1.png);
		background-repeat: no-repeat;
		height: 209px;
		width: 185px;
	}
}

.hero__item--3 {
	background: linear-gradient(112.82deg, #F68AB1 0%, #FEB5CF 100%);

	&::before {
		position: absolute;
		top: -23px;
		right: 10px;
		content: '';
		background: url(../img/hero-prod-2.png);
		background-repeat: no-repeat;
		height: 257px;
		width: 108px;
	}
}

.hero__item-text {
	margin-bottom: 15px;
	font-size: 16px;
	line-height: 122%;

	@media screen and (max-width : 767px) {
		display: none;
	}
}

.hero__item-content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	height: 100%;
}

.read-more {
	a {
		text-decoration: underline;
		text-decoration-color: rgba(255, 255, 255, 0.4);

		&:hover {
			color: #FFFFFF;
			text-decoration-color: rgba(255, 255, 255, 1);
		}
	}

	i {
		position: relative;
		top: -1px;
		display: inline-block;
		margin-left: 10px;
		font-size: 8px;
		transition: $transition;
	}

	&:hover {
		i {
			transform: translateX(3px);
		}
	}
}


.section-link {
	font-size: 16px;

	a {
		text-decoration: underline;
	}

	i {
		position: relative;
		top: -1px;
		display: inline-block;
		margin-left: 10px;
		font-size: 8px;
		transition: $transition;
	}

	&:hover {
		color: $main-accent;

		i {
			transform: translateX(3px);
		}
	}
}

.section-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 30px;
	@media screen and (max-width : 599px) {
	 flex-direction: column;
	 align-items: flex-start;
	}
}

.products-item {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-bottom: 30px;
	border: 1px solid #EEEEEE;
	border-radius: 10px;
	padding: 20px;
	width: 100%;

	@media screen and (max-width : 599px) {
		padding: 10px;
	}
}

.products-item__content-top {
	margin-bottom: 10px;
}

.products-item__content-mid {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 15px;
}

.products-item__labels {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.products-item__media {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 30px;
	height: 100%;
	text-align: center;
}

.products-item__label {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 3px;
	border-radius: 5px;
	padding: 8px;
	height: 25px;
	font-size: 12px;
	line-height: 150%;
	font-weight: bold;
	color: $main-light;

	@media screen and (max-width : 767px) {
		font-size: 10px;
	}
}

.label-sale {
	background: $ternary-accent;
}

.label-recommended {
	background: $secondary-accent;
}

.products-item__title {
	display: inline-block;
	margin-bottom: 10px;
	font-weight: 700;

	@media screen and (max-width : 991px) {
		font-size: 16px;
	}

	@media screen and (max-width : 767px) {
		font-size: 15px;
	}
}

.products-item__descr {
	font-size: 16px;
	line-height: 112.5%;
	color: $secondary-dark;

	@media screen and (max-width : 991px) {
		font-size: 15px;
	}

	@media screen and (max-width : 767px) {
		display: none;
	}
}

.products-item {
	.products-item__price-wrap {
		flex-direction: column;
		justify-content: space-between;
	}
}

.products-item__price-wrap {
	display: flex;

}

.products-item__price {
	font-size: 20px;
	font-weight: bold;

	@media screen and (max-width : 991px) {
		font-size: 18px;
	}
}

.products-item__price-sale {
	font-size: 16px;
	color: $secondary-dark;

	@media screen and (max-width : 991px) {
		font-size: 14px;
	}
}

.group-buy {
	display: flex;
	align-items: center;

	.btn-buy {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		padding-right: 14px;
		padding-left: 14px;
	}

	.quantity {
		border-top-right-radius: 0;
		border-right-width: 0;
		border-bottom-right-radius: 0;
	}
}

.products-item__btn {
	height: 40px;
	width: 100%;

	@media screen and (max-width : 599px) {
		height: 30px;
	}
}

.quantity {
	position: relative;
	border: 1px solid #DFDFDF;
	border-radius: 5px;
	padding-top: 10px;
	padding-bottom: 10px;
	height: 50px;
	color: #151515;
}

.quantity-number {
	margin-right: 30px;
	border: none;
	padding: 5px;
	height: 100%;
	width: 33px;
	font-size: 14px;
	color: $secondary-dark;
	text-align: center;


	&::-webkit-inner-spin-button,
	&::-webkit-outer-spin-button {
		margin: 0;
		-webkit-appearance: none;
	}
}

.quantity-controls {
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	margin-top: 10px;
	margin-bottom: 10px;
	border-left: 1px solid #DFDFDF;
	padding-right: 9px;
	padding-left: 9px;
	height: 30px;
	font-size: 8px;
	font-weight: 700;

	.quantity-btn {
		padding: 3px;

		&:hover {
			color: $main-accent;
			transition: $transition;
		}
	}

	.quantity-btn-up {
		transform: rotate(-90deg) translateY(1px);
	}

	.quantity-btn-down {
		transform: rotate(90deg) translateY(1px);
	}
}


.video {
	border-radius: 10px;
	height: 100%;
	width: 100%;

}

.video-wrapper {
	position: relative;
	display: table;
	border-radius: 10px;
	width: 100%;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.video-overlay {
	position: absolute;
	top: 0%;
	bottom: 0%;
	left: 0%;
	right: 0%;
	border-radius: 10px;
	background: rgba(0, 0, 0, 0.3);
	height: 100%;
	width: 100%;

	i {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		font-size: 33px;
	}
}


.sec-home-1 {
	position: relative;

	&::before {
		position: absolute;
		bottom: 0;
		left: 0;
		content: '';
		background-image: url(../img/decor-2.png);
		background-size: cover;
		background-repeat: no-repeat;
		height: 165px;
		width: 97px;

		@media screen and (max-width : 1365px) {
			display: none;
		}
	}
}

.sec-home-4 {
	position: relative;
	padding-top: 90px;
	padding-bottom: 80px;
	background: #F7F7F7;

	&::before {
		position: absolute;
		top: -100px;
		right: 0;
		content: '';
		background-image: url(../img/decor-3.png);
		background-size: cover;
		background-repeat: no-repeat;
		height: 293px;
		width: 146px;

		@media screen and (max-width : 1365px) {
			display: none;
		}
	}

	@media screen and (max-width : 767px) {
		padding-top: 40px;
	}

}

.short-about {
	.col-6 {
		@media screen and (max-width : 767px) {
			flex-basis: 100%;
			max-width: 100%;

			&:not(:last-child) {
				margin-bottom: 15px;
			}
		}
	}

	h5 {
		margin-bottom: 15px;

		@media screen and (max-width : 1199px) {
			margin-bottom: 5px;
		}
	}

	p {
		margin-bottom: 30px;
		line-height: 127.7%;

		@media screen and (max-width : 1199px) {
			margin-bottom: 10px;
			font-size: 16px;
			line-height: 112.5%;
		}
	}

	img {
		border-radius: 10px;
		box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
	}

	img+img {
		margin-left: 10px;
	}
}

.sec-testimonials {
	padding-top: 75px;
}

.testimonials-item {
	position: relative;
	border: 1px solid #EEEEEE;
	border-radius: 10px;
	padding: 20px 20px 40px 20px;

	&::before {
		position: absolute;
		bottom: 20px;
		right: 20px;
		content: '';
		background: url(../img/quote.svg);
		background-repeat: no-repeat;
		height: 50px;
		width: 64px;
	}
}

.testimonials-item__top {
	display: flex;
	align-items: center;
	margin-bottom: 15px;
}

.testimonials-item__header {
	display: flex;
	flex-direction: column;
}

.testimonials-item__title {
	margin-bottom: 5px;
	font-weight: 700;
}

.testimonials-item__info {
	font-size: 16px;
	color: $secondary-dark;
}

.testimonials-item__media {
	margin-right: 12px;

	img {
		border-radius: 50%;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	}
}

.testimonials-item__body {
	font-size: 16px;
	line-height: 125%;
}


.footer {
	margin-top: 80px;
	color: $main-light;

	@media screen and (max-width : 767px) {
		margin-top: 35px;
	}

	.logo {
		width: 270px;

		@media screen and (max-width : 991px) {
			margin-bottom: 25px;
			flex: 1 0 100%;
		}
	}

	.footer__top {
		padding-top: 25px;
		padding-bottom: 25px;
		background: #292929;
	}

	.footer__mid {
		padding-top: 45px;
		padding-bottom: 40px;
		background: #333333;
	}
}

.footer__mid-inner {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.footer__mid-col {
	@media screen and (max-width : 767px) {
		margin-bottom: 25px;
		flex: 1 0 50%;
	}
}

.footer__bot {
	padding-top: 20px;
	padding-bottom: 15px;
	background: #292929;
}

.footer__top-inner {

	display: flex;
	justify-content: space-between;
	align-content: center;

	@media screen and (max-width : 767px) {
		flex-direction: column;
	}
}

.newsletter {
	display: flex;
	justify-content: space-between;
	align-items: center;

	.newsletter-text {
		margin-right: 20px;

		@media screen and (max-width : 767px) {
			margin-bottom: 10px;
		}
	}

	@media screen and (max-width : 1199px) {
		flex-direction: column;
		width: 100%;
	}

	@media screen and (max-width : 767px) {
		margin-bottom: 20px;
	}
}

.newsletter__form {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: auto;

	.newsletter__form-input {
		border-radius: 5px;
