.control {
	position: relative;
	display: block;
	margin-bottom: 5px;
	padding-top: 3px;
	padding-left: 30px;
	font-family: arial;
	font-size: 18px;
	cursor: pointer;
}

.control input {
	z-index: -1;
	position: absolute;
	opacity: 0;
}

.control_indicator {
	position: absolute;
	top: 2px;
	left: 0;
	border: 1px solid #DFDFDF;
	border-radius: 5px;
	background: #ffffff;
	height: 20px;
	width: 20px;
}

.control:hover input~.control_indicator,
.control input:focus~.control_indicator {
	background: #eeeeee;
}

.control input:checked~.control_indicator {
	background: #F07FAE;
}

.control:hover input:not([disabled]):checked~.control_indicator,
.control input:checked:focus~.control_indicator {
	background: #F07FAE;
}

.control input:disabled~.control_indicator {
	opacity: 0.6;
	background: #e6e6e6;
	pointer-events: none;
}

.control_indicator:after {
	position: absolute;
	content: '';
	display: none;
	box-sizing: unset;
}

.control input:checked~.control_indicator:after {
	display: block;
}

.control-checkbox .control_indicator:after {
	top: 4px;
	left: 6px;
	transform: rotate(45deg);
	border: solid #ffffff;
	border-width: 0 3px 3px 0;
	height: 6px;
	width: 3px;
}

.control-checkbox input:disabled~.control_indicator:after {
	border-color: #eee;
}

