.control-radio {
	position: relative;
	display: block;
	margin-bottom: 12px;
	padding-top: 3px;
	padding-left: 30px;
	font-family: arial;
	font-size: 18px;
	cursor: pointer;
}

.control-radio input {
	z-index: -1;
	position: absolute;
	opacity: 0;
}

.control-radio_indicator {
	position: absolute;
	top: 2px;
	left: 0;
	border: 1px solid #DFDFDF;
	border-radius: 50%;
	background: #fff;
	height: 20px;
	width: 20px;
}

.control-radio:hover input~.control-radio_indicator,
.control-radio input:focus~.control-radio_indicator {
	background: #eee;
}

.control-radio input:checked~.control-radio_indicator {
	background: #fff;
}

.control-radio:hover input:not([disabled]):checked~.control-radio_indicator,
.control-radio input:checked:focus~.control-radio_indicator {
	background: #eee;
}

.control-radio input:disabled~.control-radio_indicator {
	opacity: 0.6;
	background: #e6e6e6;
	pointer-events: none;
}

.control-radio_indicator:after {
	position: absolute;
	content: '';
	display: none;
	box-sizing: unset;
}

.control-radio input:checked~.control-radio_indicator:after {
	display: block;
}

.control-radio-radio .control-radio_indicator {
	border-radius: 50%;
}

.control-radio-radio .control-radio_indicator:after {
	top: 3px;
	left: 3px;
	border-radius: 50%;
	background: #F07FAE;
	height: 12px;
	width: 12px;
	transition: background 250ms;
}

.control-radio-radio input:disabled~.control-radio_indicator:after {
	background: #eee;
}

.control-radio-radio .control-radio_indicator::before {
	z-index: 99999;
	position: absolute;
	top: 0;
	left: 0;
	content: '';
	opacity: 0.6;
	transform: scale(0);
	display: block;
	margin-top: -1.3rem;
	margin-left: -1.3rem;
	border-radius: 3rem;
	background: #2aa1c0;
	height: 4.5rem;
	width: 4.5rem;
}

