@font-face {
  font-family: "BloggerSans";
  src: url("/fonts/Blogger_Sans.woff2") format("woff2"),
       url("/fonts/Blogger_Sans.woff") format("woff");
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}
@font-face {
  font-family: "BloggerSans";
  src: url("/fonts/Blogger_Sans-Bold.woff2") format("woff2"),
       url("/fonts/Blogger_Sans-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
	font-display: swap;
}


@font-face {
  font-family: 'icomoon';
  src:  url('/fonts/icomoon.eot?whaffx');
  src:  url('/fonts/icomoon.eot?whaffx#iefix') format('embedded-opentype'),
    url('/fonts/icomoon.ttf?whaffx') format('truetype'),
    url('/fonts/icomoon.woff?whaffx') format('woff'),
    url('/fonts/icomoon.svg?whaffx#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

